.specialists {
    display: flex;
    justify-content: center;
    min-height: 100;
    width: 100vw;
    &Container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 80vw;
    }
    &TitleBox {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;

        width: 100%;
        height: 100px;
    }
    &Box {
        display: flex;
        flex-direction: row;
        align-items: center;
        width: 80vw;
        padding: 10px;

        @media screen and (max-width:600px) {
            flex-direction: column-reverse;
            margin-bottom: 25px;
        }

        &Info {
            display: flex;
            flex-direction: column;
            width: 73%;
            &Name {
                display: flex;
                font-size: 25px;
                padding: 10px;
                
                @media screen and (max-width:600px) {
                    font-size: 20px;
                    font-weight: 600;
                    padding: 4px;
                    margin-top: 10px;;
                    text-align: center;
                }

            }
            &About {
                font-size: 20px;
                padding: 10px;
                font-weight: 100;
                color: rgba(3, 3, 3, 0.637);

                @media screen and (max-width:600px) {
                    font-size: 18px;
                    font-weight: 300;
                    padding: 1px;
                    margin-top: 10px;
                    text-align: center;
                }

            }
        }
        &Picture {
            background-repeat: no-repeat;
            background-size: cover;
            background-position: right;

            width: 27%;
            height: 450px;

            @media screen and (max-width: 1400px) {
                height: 400px;
            }
            @media screen and (max-width: 1000px) {
                height: 300px;
            }
            @media screen and (max-width: 600px) {
                height: 250px;
                margin-top: 10px;
            }
            @media screen and (max-width: 600px) {
                width: 100%;
            }
        }
        
    }
}


