.AppWrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;


  background-color: #f5f5fd;
}

.AppForm {
  position: fixed;
  height: 100%;
  // overflow-y: hidden;
}

.container {
  width: 90%;
}

a {
  color: black;
  text-decoration: none;
}

body {
  font-family: 'Roboto', sans-serif;
  min-height: 100%;
}

.formButton {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 190px;
  height: 44px;
  outline: none;
  border: none;

  @media screen and (max-width: 650px) {
    width: 90%;
  }
  
  &:focus {
    outline: none;
  }

  

  &Fixed {
    position: fixed;
    bottom: 5%;
    right: 5%;
  }

  &Stop {
    position: relative;
    z-index: 100;
  }

  background: linear-gradient(90deg, rgba(79, 172, 254, 0.6) 0%, rgba(0, 242, 254, 0.6) 100%);
  border-radius: 10px;

  span {
    font-family: 'Roboto', sans-serif;
    font-weight: 600;
    color: #464343;
    font-size: 14px;
  }
  
}

.phoneLogo {
  width: 20px;
  height: 20px;
  
  background-image: url(./images/phone.png);
  background-repeat: no-repeat;
  background-size: contain;
  margin-right: 6px;
}