.about {
    display: flex;
    flex-direction: row;
    justify-content: center;

    width: 100%;
    min-height: 100vh;

    &Container {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &TitleBox {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;

        width: 100%;
        height: 100px;

        // margin-bottom: 100px;
    }

    &Block {
        display: flex;
        align-items: center;

        justify-content: space-between;

        @media screen and (max-width: 600px) {
            flex-direction: column;
        }

        &:nth-child(odd) {
            flex-direction: row-reverse;

            @media screen and (max-width: 600px) {
                flex-direction: column;
            }
        }

        width: 100%;

        margin-bottom: 50px;

        &Text {
            font-size: 23px;
            line-height: 33px;
            max-width: 50%;

            @media screen and (max-width: 600px) {
                min-width: 100%;
                font-size: 17px;
                line-height: 26px;
                margin-bottom: 20px;
            }
        }

        &Image {
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;

            width: 45%;
            height: 450px;

            @media screen and (max-width: 1400px) {
                height: 400px;
            }
            @media screen and (max-width: 1000px) {
                height: 300px;
            }
            @media screen and (max-width: 600px) {
                height: 250px;
            }
            @media screen and (max-width: 600px) {
                width: 100%;
            }
        }
    }
}

