.footer {
    display: flex;
    flex-direction: row;
    justify-content: center;

    grid-template: footer;

    height: 76px;
    background-color: #010101;
    border-top: 1px solid black;

    &Container {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        color: white;
    }
}