.header {
    display: flex;
    flex-direction: row;
    justify-content: center;

    grid-area: header;

    position: fixed;
    left:0;
    right:0;
    top:0;

    z-index: 15;

    width: 100%;
    height: 46px;

    background: #FFFFFF;
    border-bottom: 1px solid #EEEEEE;
    box-sizing: border-box;
    
    a {
        color: black;
    }

    &Container {
        display: flex;
        flex-direction: row;
        align-items: center;
    }

    &Logotype {
        display: flex;
        flex-direction: row;
        align-items: center;

        &Image {
            background: url(../../images/logo.png);
            width: 36px;
            height: 36px;
            margin-right: 8px;
        }

        &Text {
            display: flex;
            flex-direction: column;
            justify-content: center;

            &Title {
                font-size: 18px;
                line-height: 21px;
                color: #545454;
            }

            &Desc {
                font-size: 10px;
                line-height: 10px;
                color: #828282;
            }
        }
    }

    &WhiteSpace {
        display: flex;
        flex-grow: 1;
    }

    &Contacts {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 350px;

        @media screen and (max-width: 1300px) {
            display: none;;
        }

        &Adress {
            display: flex;
            flex-direction: row;
            align-items: center;

            &Image {
                background: url(../../images/location.png);
                background-size: cover;
                background-repeat: no-repeat;
                width: 14px;
                height: 17px;
                margin-right: 8px;
            }

            &Text {
                font-weight: 300;
                font-size: 14px;
                line-height: 16px;
            }
        }

        &Phone {
            display: flex;
            flex-direction: row;
            align-items: center;

            &Image {
                background: url(../../images/phone.png);
                background-size: cover;
                background-repeat: no-repeat;
                width: 17px;
                height: 17px;
                margin-right: 8px;
            }

            &Text {
                font-weight: 300;
                font-size: 14px;
                line-height: 16px;
            }
        }
    }

    &Menu {
        display: flex;
        flex-direction: row;
        align-items: center;

        @media screen and (max-width: 650px) {
            display: none;
        }

        &Link {
            display: flex;
            flex-direction: row;
            justify-content: center;
            width: 150px;
            height: 22px;
            font-weight: 300;
            font-size: 14px;
            line-height: 22px;
            margin-right: 20px;
            border-radius: 4px;

            &:last-child {
                margin-right: 0px;
            }

            &:hover {
                background: linear-gradient(270deg, rgba(90, 90, 90, 0.05) 0%, rgba(41, 41, 41, 0.05) 101.95%);
            }

            &Selected {
                background: linear-gradient(270deg, rgba(132, 132, 132, 0.15) 0%, rgba(80, 80, 80, 0.15) 101.95%);

                &:hover {
                    background: linear-gradient(270deg, rgba(132, 132, 132, 0.15) 0%, rgba(80, 80, 80, 0.15) 101.95%);
                }
            }
        }
    }

    &Mobile {
        
        background: url(../../images/cil_hamburger-menu.png);
            background-size: cover;
            background-repeat: no-repeat;
            width: 30px;
            height: 30px;

        @media screen and (min-width: 650px) {
            display: none;

            
        }

        &Close {
            background-image: url(../../images/close.png);
            background-size: 19px 19px;
            background-position: center;
            background-repeat: no-repeat;
            width: 30px;
            height: 30px;
        }
        
    }
}
