.mobileMenu {
    display: flex;
    flex-direction: column;
    align-items: right;
    position: relative;

    width: 60%;
    height: 100%;

    background-color: #FFFFFF;
    border-left: 1px solid #EEEEEE;

    padding-top: 90px;
    padding-right: 30px;
    animation: show-div .2s 1;
    

    z-index: 10;

    @keyframes show-div {
        0% {
            right: -600px;
        }
        100% {
            right: 0;
        }
      }

    &Row {
        margin-bottom: 30px;
        display: flex;
        justify-content: flex-end;

        a {
            color: black;
            font-size: 16px;
            text-align: right;
        }
        
    }
    

    &Overlay {
        display: flex;
        justify-content: flex-end;
        position: fixed;

        background-color: rgba($color: #000000, $alpha: 0.4);

        width: 100%;
        height: 100%;

        top: 0;
        left: 0;
        overflow: hidden;

        z-index: 9;
    }
}