.header {
    position: fixed;
    width: 300px;
    height: 100%;
    background-color:#222;
    color: white;
    padding: 25px 10px 25px 10px;

    box-shadow: 3px 0px 8px 0px rgba(34, 60, 80, 0.56);
    h3 {
        font-weight: 300;
    }

    a {
        color: white;
    }

    &Menu {
        display: flex;
        flex-direction: column;
        h4 {
            font-weight: 700;
        }
        margin-bottom: 40px;
        a {
            margin-bottom: 6px;
        }
    }
}

.adminpanelname {
    margin-bottom: 40px;
}

.selectedLink {
    font-weight: bold;
    font-size: 16px;
}