.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  min-height: 100vh;

  &Container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &Banner {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    
  }

  &Box {
    display: flex;
    flex-direction: row;
    width: 80%;
    height: 125px;
    align-items: center;
    justify-content: center;

    @media screen and (max-width: 600px) {
      display: flex;
      flex-direction: column;
      align-items: center;
      height: auto;
    }

    &Words {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 50%;
      justify-content: center;
      align-items: flex-start;

      @media screen and (max-width: 600px) {
        margin-bottom: 100px;
      }
      &One {
        font-size: 25px;
        font-family: "Roboto", sans-serif;
        height: 45%;
        width: 100%;
        text-align: left;
        font-weight: bold;
        color: #424242;
        @media screen and (max-width: 600px) {
          font-size: 20px;
          text-align: center;
        }
      }
      &Two {
        font-family: "Roboto",sans-serif;
        font-size: 21px;
        text-align: left;
        width: 100%;
        height: 45%;
        margin-top: 20px;
        font-weight: normal;
        @media screen and (max-width: 600px) {
          font-size: 16px;
          text-align: center;
        }
      }
    }

    &Image {
      display: flex;
      width: 270px;
      height: 85px;
      align-items: center;
      justify-content: center;
      background: url(../../images/Teeth.png);
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;

      @media screen and (max-width: 600px) {
        width: 220px;
        height: 65px;
      }
    }
  }
}
