.form {
    display: flex;
    flex-direction: row;
    justify-content: center;
    
    height: 100%;
    width: 100%;

    position: fixed;
    top: 0;
    left: 0;
    overflow: hidden;

    background-color: rgba($color: #000000, $alpha: 0.4);
    z-index: 100;

    font-family: 'Roboto', sans-serif;

    &Container {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }

    &Box {
        display: flex;
        flex-direction: column;
        align-items: center;

        position: relative;

        width: 650px;
        height: 650px;

        background-color: #fff;
        border-radius: 6px;
        border: 1px solid #e1e1e1;

        

        h1 {
            margin-bottom: 40px;
            color: #292929;

            @media screen and (max-width: 600px) {
                font-size: 20px;
            }
        }

        &Header {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: flex-end;

            padding: 20px;

            height: 70px;
            width: 100%;

        }

        &Close {
            height: 18px;
            width: 18px;

            background-image: url(../../images/close.png);
            background-color: transparent;
            background-size: contain;
            background-repeat: no-repeat;

            outline: hidden;
            border: none;
        }

        &Body {
            display: flex;
            flex-grow: 1;
            flex-direction: column;
            align-items: center;

            width: 100%;

            padding: 0px 85px;

            @media screen and (max-width: 600px) {
                padding: 0 24px
            }

        }

        &Footer {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;

            padding: 20px;

            height: 60px;
            width: 100%;
            border-top: 1px solid #e1e1e1;

            &CheckBox {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: center;
                height: 45px;
                

                &Input {
                    width: 18px;
                    height: 18px;
                    margin-right: 10px;

                    @media screen and (max-width: 600px) {
                        height: 32px;
                        width: 32px;
                        font-size: 15px;
                    }
                }

                span {
                    font-family: 'Roboto', sans-serif;
                    font-size: 14px;
                    font-weight: bolder;
                    color: #292929;
                    @media screen and (max-width: 600px) {
                        font-size: 12px;
                        line-height: 14px;
                    }
                }

                &Button {
                    width: 120px;
                    height: 30px;
                    border-radius: 6px;     
                    border: 1px solid #004799;
                    outline: none;

                    color: white;
                    font-family: 'Roboto', sans-serif;
                    background-color: #0075ff; 

                    &:hover {
                        background-color: #1381fd; 
                    }
                    &:active {
                        background-color: #0075ff; 
                    }

                    &:disabled {
                        background-color: #8cc2ff;
                        border: 1px solid #81aee2;
                    }
                }
            }
        }

        &DateRow {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;

            width: 100%;
        }
    }
    

    &Input {
        width: 100%;
        height: 48px;

        margin-bottom: 20px;

        border-radius: 6px;
        outline: none;
        border: 1px solid #e1e1e1;

        font-size: 18px;
        padding-left: 12px;

        @media screen and (max-width: 600px) {
            height: 38px;
            font-size: 15px;
        }

        &:focus {
            outline: blue;
        }

        &Short {
            width: 49%;
            height: 48px;

            margin-bottom: 20px;

            border-radius: 6px;
            outline: none;
            border: 1px solid #e1e1e1;

            font-size: 18px;
            padding-left: 12px;

            @media screen and (max-width: 600px) {
                height: 38px;
                font-size: 15px;
            }

        }
    }
}

.hide {
    display: none;
}