.contacts {
  display: flex;
  flex-direction: row;
  justify-content: center;

  width: 100%;

  &Container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &Map {
    width: 55%;
    @media screen and (max-width: 1300px) {
      width: 100%;
    }

    height: 600px;

    iframe {
      width: 100%;
      height: 100%;
    }
  }

  &TitleBox {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    
    width: 100%;
    height: 100px;

    font-size: 27px;

    h1 {
      font-weight: 400;
      font-size: 26px;
      color: #424242;
    }


  }

  &Box {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    @media screen and (max-width: 1300px) {
      flex-direction: column;
      margin-bottom: 50px;
    }

    background:#ffffff;
    padding: 0 10px;

    width: 100%;

    border-radius: 6px;
    border: solid 1px #dcdcdc;

    // -webkit-box-shadow: 49px -16px 37px -9px rgba(34, 60, 80, 0.18);
    // -moz-box-shadow: 49px -16px 37px -9px rgba(34, 60, 80, 0.18);
    // box-shadow: 49px -16px 37px -9px rgba(34, 60, 80, 0.18);

    &Info {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      flex-grow: 1;
      // background: blueviolet;
      height: 600px;
      margin: 7px;
      border: #545454;

      &Adress {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        // padding: 23px;
        // margin: 7px;
        width: 100%;

        

        &Name {
          justify-content: left;
          font-size: 14pt;
          width: 100%;
          // background: burlywood;
          text-align: center;
          margin-right: 10px;
          margin-bottom: 10px;

          @media screen and (max-width: 1300px) {
            font-size: 14px;
          }
        }

        &Word {
          justify-content: right;
          font-size: 14pt;
          // background:red ;
          width: 100%;
          text-align: center;

          @media screen and (max-width: 1300px) {
            font-size: 14px;
          }

        }
      }

      &Phone {
        display: flex;
        flex-direction: row;
        margin: 7px;
        justify-content: center;
        padding: 23px 13px;
        width: 100%;

        &NumberWord {
          font-size: 14pt;
          width: 45%;
          text-align: right;
          margin-right: 10px;

          @media screen and (max-width: 1300px) {
            font-size: 14px;
          }
        }

        &Number {
          font-size: 14pt;
          width: 45%;
          text-align: left;

          @media screen and (max-width: 1300px) {
            font-size: 14px;
          }
        }
      }
      &Time {
        display: flex;
        flex-direction: column;
        width: 100%;

        &Week {
          display: flex;
          flex-direction: row;
          padding: 13px;
          margin: 7px;
          justify-content: center;
          
          

          &Days {
            font-size: 14pt;
            width: 45%;
            text-align: right;
            margin-right: 10px;
            @media screen and (max-width: 1300px) {
              font-size: 14px;
            }
          }

          &Watch {
            font-size: 14pt;
            width: 45%;
            text-align: left;
            @media screen and (max-width: 1300px) {
              font-size: 14px;
            }
          }
        }

        &Sut {
          display: flex;
          flex-direction: row;
          padding: 13px;
          margin: 7px;
          justify-content: center;
          width: 100%;

          &Day {
            font-size: 14pt;
            width: 45%;
            text-align: right;
            margin-right: 10px;
            @media screen and (max-width: 1300px) {
              font-size: 14px;
            }
          }

          &Watch {
            font-size: 14pt;
            width: 45%;
            text-align: left;
            @media screen and (max-width: 1300px) {
              font-size: 14px;
            }
          }
        }

        &Sun {
          display: flex;
          flex-direction: row;
          padding: 7px;
          margin: 7px;
          justify-content: center;
          width: 100%;

          &Day {
            font-size: 14pt;
            text-align: right;
            width: 48%;
            margin-right: 10px;
            @media screen and (max-width: 1300px) {
              font-size: 14px;
            }
          }

          &Watch {
            font-size: 14pt;
            width: 48%;
            text-align: left;
            @media screen and (max-width: 1300px) {
              font-size: 14px;
            }
          }
        }
      }

      &About {
        font-size: 14pt;
        display: flex;
        flex-direction: row;
        justify-content: center;
        margin: 20px 0;
        font-weight: 600;
        @media screen and (max-width: 1300px) {
          font-size: 16px;
        }
      }
    }
  }
}

