.price {
    display: flex;
    flex-direction: row;
    justify-content: center;

    min-height: 100vh;
    width: 100%;

    &Container {
        display: flex;
        flex-direction: column;
        align-items: center;

        h1 {
            font-weight: 400;
            font-size: 26px;
            color: #424242;
        }
    }

    &TitleBox {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;

        width: 100%;
        height: 100px;
    }

    &SelectRow {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;

        width: 100%;
        height: 100px;
    }

    &SelectBox {
        display: flex;
        flex-direction: column;
        align-items: left;
        justify-content: center;
        padding:4px;
        background-color: #eeeef8;
        border: none;
        border-radius: 6px;
        width: 260px;

        @media screen and (max-width: 600px) {
            width: 100%;
            align-items: center;
            margin-bottom: 25px;
        }
        

        h1 {
            font-weight: 600;
            font-size: 32px;
            line-height: 38px;
            color: #424242;

        }


        &Selector {
            width: 250px;
            height: 20px;

            font-weight: 600;
            font-size: 14px;
            line-height: 14px;

            border: none;
            border-radius: 4px;
            // background-color: transparent;
            
    
            &:focus {
                outline: none;
            }
        }
    }

    &ServicesBox {
        display: flex;
        flex-direction: column;
        align-items: center;

        width: 100%;

        &Block {
            display: flex;
            flex-direction: column;
            align-items: center;

            width: 100%;

            &Title {
                display: flex;
                justify-content: center;

                width: 100%;
                height: 70px;
                
                

                h1 {
                    font-weight: 600;
                    font-size: 26px;
                    line-height: 30px;
                    text-align: center;
                    color: #424242;
                    @media screen and (max-width: 600px) {
                        font-size: 18px;
                    }
                }
            }

            &Table {
                display: flex;
                flex-direction: column;
                align-items: center;

                width: 100%;

                &Row {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: space-between;

                    width: 100%;
                    height: 52px;

                    
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 15px;
                    color: #424242;

                    padding: 0 20px;

                    @media screen and (max-width: 600px) {
                        font-size: 13px;
                    }

                    

                    &Name {
                        flex-grow: 1;
                        @media screen and (max-width: 600px) {
                            max-width: 250px;
                        }
                    }

                    &Price {
                        width: 170px;

                        @media screen and (max-width: 600px) {
                            width: 80px;
                        }
                    }

                    &:first-child {
                        font-weight: 600;
                        font-size: 16px;
                        line-height: 26px;
                        color: #424242;
                        border-radius: 10px 10px 0 0;

                        @media screen and (max-width: 600px) {
                            font-size: 14px;
                        }
                    } 

                    &:nth-child(odd) {
                        background-color: #eeeef8;
                    }
                    &:nth-child(even) {
                        background-color: #e1e1ee;
                    }
                    &:last-child {
                        border-radius: 0 0 10px 10px ;
                        margin-bottom: 40px;
                    }
                }
            }
        }
    }
}