.reviews {
    display: flex;
    width: 100vw;
    justify-content: center;
    &Container {
        display: flex;
        flex-direction: column;
        width: 80vw;
        align-items: center;
        justify-content: space-between;
    }
    &TitleBox {
        display: flex;
        padding: 10px;
    }
    &EnterBox {
        display: flex;
        flex-direction: column;
        height: 25vh;
        width: 60%;
        justify-content: center;
        align-items: center;
        font-size: 21px;
        font-weight: 600;
    }
}